
.legal-page {
    padding: 70px 0px;
    background: #f5f8f8;
}

.legal-content h2 {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    /* identical to box height, or 40px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0px auto;
    text-align: center;
    padding-bottom: 50px;
}

.legal-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* grey 2 */

    color: #6e6f6f;
    margin: 0;
    padding-bottom: 25px;
}

.legal-content h3 {
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    /* or 31px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    padding-bottom: 30px;
    padding-top: 15px;
}


.legal-disclaimer{
    background: #fff;
    padding: 55px 0px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;
    color: #6e6f6f;
    margin: 0;
}



/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/




@media screen and (max-width:767px){


.legal-page {
    padding: 60px 0px 35px;
}

.legal-content h2 {
    font-size: 26px;
    padding-bottom: 40px;
}

.legal-content h3 {
    font-size: 20px;
}

.legal-disclaimer{
    padding: 30px 0px;
}
}