.about-banner {
    padding-top: 67px;
    padding-bottom: 186px;
}

.about-banner-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}

.about-banner-content {
    width: calc(100% - 600px);
}

.about-bubble {
    background: linear-gradient(180deg, rgba(243, 92, 92, 0.15) 0%, rgba(243, 92, 92, 0) 100%);
    width: 302px;
    height: 302px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px;
    text-align: center;
}

.about-bubble span {
    font-weight: 400;
    font-size: 24.0537px;
    line-height: 26px;
    /* or 108% */

    text-align: center;
    letter-spacing: -0.01em;

    /* text */

    color: #333333;
    display: block;
}

.about-banner-cover {
    max-width: 450px;
    position: relative;
    min-height: 400px;
    width: 100%;
}

.about-banner-cover img {
    position: absolute;
    right: 0;
    bottom: 0;
}

.about-banner-content h2 {
    font-style: normal;
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    /* identical to box height, or 40px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    padding-bottom: 60px;
}

.about-banner-content p {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* grey 2 */

    color: #6e6f6f;
    margin: 0;
    padding-bottom: 25px;
}



.about-experience-section {
    padding: 50px 0px 100px;
    background: #f5f8f8;
    position: relative;
}

.about-experience-section:before{
    content: "";
    background: url(../../assets/images/about/shape2.svg);
    position: absolute;
    top: -90px;
    left: 0;
    right: 0;
    width: 100%;
    height: 150px;
    background-size: cover;
    background-position: center;

}

.about-experience {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 50px;
}

.about-experience-content {
    width: calc(100% - 490px);
}

.about-experience-cover {
    max-width: 390px;
    width: 100%;
    position: relative;
}

.about-experience-content h2 {
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    /* or 31px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    padding-bottom: 50px;
}

.about-experience-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* grey 2 */

    color: #6e6f6f;
    margin: 0;
    padding-bottom: 25px;
}

.about-experience-cover img {
    max-width: 390px;
    width: 100%;
    position: relative;
    z-index: 9;
}

.about-experience-cover:after {
    content: "";
    background: linear-gradient(180deg, rgba(243, 92, 92, 0.15) 0%, rgba(243, 92, 92, 0) 100%);
    width: 378px;
    height: 378px;
    display: block;
    border-radius: 100%;
    position: absolute;
    bottom: -111px;
    right: -150px;
}

.about-who-we-are {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.about-who-we-are-cover {
    max-width: 390px;
    width: 100%;
    position: relative;
}

.about-who-we-are-content {
    width: calc(100% - 490px);
}

.about-who-we-are-content h2 {
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #333333;
    margin: 0;
    padding-bottom: 50px;
}

.about-who-we-are-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;
    color: #6e6f6f;
    margin: 0;
    padding-bottom: 25px;
}

.about-who-we-are-cover img {
    max-width: 390px;
    width: 100%;
    position: relative;
    z-index: 9;
}

.about-who-we-are-cover:after {
    content: "";
    background: linear-gradient(180deg, rgba(98, 67, 206, 0.15) 0%, rgba(98, 67, 206, 0) 100%);
    width: 307px;
    height: 307px;
    display: block;
    border-radius: 100%;
    position: absolute;
    bottom: -54px;
    left: -210px;
}

.about-team {
    padding-top: 100px;
    text-align: center;
    padding-bottom: 150px;
}

.about-team h2 {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    /* identical to box height, or 40px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    padding-bottom: 50px;
}

.about-team p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    text-align: center;
    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* grey 2 */

    color: #6e6f6f;
    max-width: 900px;
    margin: 0px auto;
    padding-bottom: 25px;
}

.team-cover {
    max-width: 692px;
    margin: 0px auto;
    margin-top: 15px;
    position: relative;
}

.team-cover img{
   width: 100%;
   position: relative;
   z-index: 9;
}

.team-cover:after {
    content: "";
    background: linear-gradient(180deg, rgba(243, 92, 92, 0.15) 0%, rgba(243, 92, 92, 0) 100%);
    width: 306px;
    height: 306px;
    display: block;
    border-radius: 100%;
    position: absolute;
    bottom: -50px;
    right: -150px;
}

.about-rcic {
    padding: 15px 0px 75px;
    background: #f5f8f8;
    position: relative;
}

.about-rcic:before{
    content: "";
    background: url(../../assets/images/about/shape1.svg);
    position: absolute;
    top: -90px;
    left: 0;
    right: 0;
    width: 100%;
    height: 150px;
    background-size: cover;
    background-position: center;
    z-index: 9;

}


.about-rcic-grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 33px;
    
    position: relative;
    z-index: 9;

}

.about-rcic-content {
    width: calc(100% - 465px);
}

.about-rcic-cover {
    max-width: 315px;
    width: 100%;
}

.about-rcic-content h2 {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    /* identical to box height, or 40px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    padding-bottom: 50px;
}

.about-rcic-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* grey 2 */

    color: #6e6f6f;
    margin: 0;
    padding-bottom: 25px;
}

.about-rcic-cover img {
    width: 100%;
}

.about-rcic h3 {
    font-weight: 400;
    font-size: 28px;
    line-height: 120%;
    /* or 31px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    max-width: 570px;
    padding-bottom: 50px;
}

.about-rcic p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* grey 2 */

    color: #6e6f6f;
    margin: 0;
    padding-bottom: 25px;
}

.about-rcic ul li {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;
    color: #6e6f6f;
    margin: 0;
    padding-bottom: 5px;
}






/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/









@media screen and (max-width:767px){

    .about-banner {
        padding-top: 20px;
        padding-bottom: 80px;
    }

    .about-banner-content {
        width: 100%;
    }
    
    .about-banner-cover {
        margin: 0px auto;
    }

    .about-experience-content {
        width: 100%;
    }

    .about-experience-cover {
        margin: 0px auto;
    }

    .about-who-we-are-content {
        width: 100%;
        order: 1;
    }

    .about-who-we-are-cover {
        margin: 0px auto;
        order: 2;
    }

    .about-rcic-content {
        width: 100%;
    }

    .about-rcic-cover {
        margin: 0px auto;
        max-width: 241px;
    }

    .about-experience-section {
        padding: 50px 0px 60px;
    }

    .about-bubble {
        width: 230px;
        height: 230px;
        padding: 20px;
    }

    .about-bubble span {
        font-size: 21.0339px;
        line-height: 24px;
    }
    
    .about-banner-cover img {
        max-width: 128px;
    }
    .about-banner-cover {
        max-width: 300px;
        min-height: 300px;
    }

    .about-banner-content h2 {
        padding-top: 14px;
        padding-bottom: 40px;
        font-size: 26px;
        text-align: center;
    }

    .about-experience-content h2 {
        font-size: 20px;
        text-align: center;
        padding-bottom: 40px;
    }

    .about-experience-section:before {
        top: -60px;
        height: 60px;
    }

    .about-who-we-are-content h2 {
        font-size: 20px;
        text-align: center;
        padding-bottom: 40px;
    }

    .about-team {
        padding-top: 67px;
        
    padding-bottom: 80px;
    }

    .about-team h2 {
        font-size: 26px;
        padding-bottom: 40px;
    }

    .about-team p {
        text-align: left;
    }


    .about-rcic-content h2 {
        font-size: 26px;
        padding-bottom: 40px;
        text-align: center;
    }

    .about-rcic:before {
        top: -60px;
        height: 70px;
    }

    .about-rcic h3 {
        font-size: 20px;
        padding-bottom: 25px;
        text-align: center;
    }

    .about-rcic {
        padding: 20px 0px 40px;
    }
}