
.footer {
    background: #333333;
    padding: 83px 0px 40px;
}

.bottom-footer {
    position: relative;
}

.bottom-footer p {
    margin: 0;
    color: #CED0D0;
    font-size: 16px;
}

.top-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer-menu ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.footer-menu ul li {
    padding-right: 70px;
}

.footer-menu ul li a {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em;
    font-feature-settings: 'calt' off;
    color: #FFFFFF;
    text-decoration: none;
}

.footer-social ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.footer-social ul li {
    margin-left: 22px;
}

.bottom-footer {
    position: relative;
    padding-top: 30px;
    display: grid;
    justify-content: space-between;
    gap: 20px 35px;
    grid-template-columns: repeat(5,minmax(0,1fr));
}

.footer-grid p {
    font-weight: 500;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: -0.01em;
    margin: 0;
    color: #6E6F6F;
}

.footer-grid p img{
    margin-right: 10px;
}

.footer-grid ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
}

.footer-grid ul li a {
    font-style: italic;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    font-feature-settings: 'calt' off;
    color: #6E6F6F;
    text-decoration: none;
    padding-bottom: 5px;
    display: block;
}

.footer-goto {
    display: flex;
    justify-content: flex-end;
    cursor: pointer;
    transition: all 0.5s;
}

.footer-goto img{
    width: 45px;
}
.footer-goto:hover {
    opacity: 0.8;
}

.footer-search {
    cursor: pointer;
}




/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/









@media screen and (max-width:767px){

    .footer {
        padding: 80px 0px 60px;
    }

    .top-footer {
        flex-wrap: wrap;
    }

    .footer-logo {
        width: 100%;
    }
    
    .footer-menu {
        width: 100%;
        padding-top: 46px;
    }
    
    .footer-search {
        width: 100%;
    }
    
    .footer-social {
        width: 100%;
        padding-top: 30px;
    }
    
    .footer-logo img {
        display: block;
        margin: 0px auto;
    }
    
    .footer-menu ul {
        flex-wrap: wrap;
    }
    
    .footer-menu ul li {
        padding: 0;
        width: 100%;
        text-align: center;
    }
    
    .footer-menu ul li a {
        padding-bottom: 32px;
        display: block;
    }
    
    .footer-search img {
        width: 16px;
        margin: 0px auto;
        display: block;
    }
    
    .footer-social ul {
        max-width: 200px;
        margin: 0px auto;
    }
    
    .footer-social ul li {
        margin: 0;
    }
    
    .footer-social ul li img {
        width: 40px;
    }
    
    .bottom-footer {
        gap: 26px;
        grid-template-columns: repeat(1, minmax(0, 1fr));
        max-width: 220px;
        margin: 0px auto;
        padding-top: 50px;
    }
    
    .footer-goto {
        position: absolute;
        bottom: -40px;
        right: -30px;
    }
    

}