
.review-page {
    padding: 100px 0px 0px;
}

.review-content-grid{
    padding-bottom: 83px;
}

.review-page h3 {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #333333;
    margin: 0;
    text-align: center;
    padding-bottom: 50px;
}

.review-content-grid {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(3, minmax(0, 1fr));
}



.review-content {
    background: #FFFFFF;
    border-radius: 10px;
    padding: 30px 20px;
    transition: all 0.5s;
    box-shadow: 0px 24.5184px 32.0625px rgb(0 0 0 / 10%);
}
.review-review {
    display: flex;
    padding: 20px 0px;
}

.review-content h4 {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #333333;
    padding-bottom: 20px;
}

.review-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #333333;
    opacity: 0.7;
    margin: 0;
    padding-bottom: 20px;
}

.review-content p:last-child {
    padding-bottom: 0px;
}

.review-content, .review-cover{
    margin-bottom: 30px;
}

.review-cover img{
    border-radius: 10px;
    width: 100%;
}




/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/





@media screen and (max-width:767px){

    .review-page {
        padding: 60px 0px 0px;
    }
    .review-page h3{
        font-size: 26px;
        padding-bottom: 40px;
    }

    .review-content-grid{
        padding-bottom: 60px;
    }
    
.review-content-grid {
    grid-template-columns: repeat(1, minmax(0, 1fr));
}

.review-content h4 {
    font-size: 18px;
}

.review-cover img {
    height: 250px;
    object-fit: cover;
    object-position: unset;
}

}