.worker-visa-page {
    padding: 70px 0px 100px;
}

.worker-visa-banner-heading {
    display: flex;
    align-items: center;
    padding-bottom: 50px;
}

.worker-visa-banner-heading-title {
    padding-left: 30px;
}

.worker-visa-banner-heading-title h5 {
    font-family: "Raleway", sans-serif !important;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    /* or 171% */

    letter-spacing: -0.01em;
    text-transform: uppercase;
    font-feature-settings: "calt" off;

    /* text */

    color: #333333;
    margin: 0;
}

.worker-visa-banner-heading-title h2 {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    /* or 40px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
}

.worker-visa-banner p {
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    /* or 150% */

    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* grey 2 */

    color: #6e6f6f;
    margin: 0;
    padding-bottom: 25px;
}

.worker-grid {
    padding-top: 25px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    padding-bottom: 100px;
}

.worker-cover {
    max-width: 514px;
    width: 100%;
    position: relative;
}

.worker-cover:after {
    content: "";
    background: linear-gradient(180deg, rgba(98, 67, 206, 0.2) 0%, rgba(98, 67, 206, 0) 100%);
    width: 368px;
    height: 368px;
    display: block;
    border-radius: 100%;
    position: absolute;
    bottom: -60px;
    right: -60px;
}


.worker-content {
    width: calc(100% - 514px);
}

.worker-content p {
    max-width: 570px;
}

.worker-cover img {
    max-width: 390px;
    width: 100%;
    position: relative;
    z-index: 9;
}



/******************** Visa Contact ***************/


.visa-contact-content {
    background: #ffffff;
    /* cards shadow */

    box-shadow: 0px 24.5184px 32.0625px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding: 50px;
    position: relative;
    margin-bottom: -35px;
}

.visa-contact-content h2 {
    margin: 0;
    padding-bottom: 20px;
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    /* or 31px */

    text-align: center;
    letter-spacing: 0.01em;

    /* text */

    color: #333333;
}

.visa-contact-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height, or 150% */

    text-align: center;
    letter-spacing: -0.01em;
    font-feature-settings: "calt" off;

    /* text */

    color: #333333;

    opacity: 0.7;
    margin: 0;
    padding-bottom: 20px;
}

.visa-contact-form form {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(4, minmax(0, 1fr));
}

.visa-contact-form form .form-control {
    background: #f5f8f8;
    border-radius: 122px;
    border-color: #f5f8f8;
    height: 54px;
    padding: 10px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height, or 150% */

    letter-spacing: -0.01em;

    /* grey 1 */

    color: #ced0d0;
}

.visa-contact-form form .form-control::placeholder {
    color: #ced0d0;
}

.visa-contact-form form .btn {
    background: #f35c5c;
    /* btn shadows */

    border-radius: 33px;
    border-color: #f35c5c;
    height: 55px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height, or 150% */

    letter-spacing: -0.01em;

    color: #ffffff;
    box-shadow: none!important;
    transition:  all 0.5s;
    position: relative;
}

.visa-contact-form form .btn:after{
    content: "";
    background: url(../../assets/images/header/btn-arrow-white.svg);
    width: 16px;
    height: 16px;
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 8px;
    position: relative;
    top: 2px;
    display: none;

}

.visa-contact-form form .btn:hover {
    background: #6243CE!important;
    border-color: #6243CE!important;
}

.visa-contact-form form .btn:hover:after {
    display: inline-block;
}

.visa-contact-form form .btn:focus {
    background: #CED0D0!important;
    border-color: #CED0D0!important;
}

.visa-contact-form form .btn:focus:hover:after{
    display:none
}
