/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/









@media screen and (max-width:767px){


    .express-visa-banner {
        padding: 40px 0px 100px;
    }
    .express-visa-banner-heading {
        justify-content: center;
        padding-bottom: 40px;
    }

    .express-visa-banner-heading-title h2 {
        font-size: 26px;
    }

    .express-how-work-content {
        width: 100%;
    }
    .express-how-work-cover {
        margin: 0px auto;
    }
    
    .express-how-work-content h3 {
        font-size: 20px;
        padding-bottom: 30px;
        text-align: center;
    }

    .express-how-work {
        padding: 20px 0px
    }

    .how-work-flip .express-how-work-content {
        order: 1;
    }

    .how-work-flip .express-how-work-cover {
        order: 2;
    }

    .express-program-content {
        width: 100%;
    }

    .express-program {
        padding: 60px 0px 60px;
    }

    .express-program-content h3 {
        font-size: 20px;
        text-align: center;
        padding-bottom: 43px;
    }

    .express-program-content h4 {
        font-size: 18px;
        text-align: center;
        padding-bottom: 30px;
    }

    .student-program:before {
        top: -60px;
        height: 70px;
    }
    .express-program.student-program {
        padding-top: 20px;
    }

    .express-program-content ul li:before {
        top: 4px;
    }
    .express-how-work:before {
        top: -60px;
        height: 70px;
    }


    .worker-visa-page {
        padding: 40px 0px 0px;
    }

    .worker-visa-banner-heading {
        justify-content: center;
        padding-bottom: 40px;
    }


    .worker-visa-banner-heading-title h2 {
        font-size: 26px;
    }

    .worker-content {
        width: 100%;
    }

    .worker-content p {
        max-width: 100%;
    }

    .worker-cover {
        margin: 0px auto;
        padding-top: 15px;
    }

    .worker-grid {
        padding-top: 15px;
        padding-bottom: 87px;
    }





    .visa-contact-content {
        padding: 20px;
        box-shadow: 0px 24.5184px 32.0625px rgb(0 0 0 / 10%);
        z-index: 9;
    }

    .visa-contact-content h2 {
        font-size: 20px;
        padding-bottom: 15px;
    }

    .visa-contact-content p {
        padding-bottom: 15px;
    }

    .visa-contact-form form {
        grid-template-columns: repeat(1, minmax(0, 1fr));
    }

    .visa-contact-form form .form-control{
        font-size: 14px;
    }
}