.header{
    position: relative;
    z-index: 999;
}

a{
    text-decoration: none!important;
}
a:hover{
    color: #6243CE!important;
}

.white-header.is-sticky .main-navigation {
    position: fixed;
    background: rgb(51 51 51 / 70%);
    top: 0;
    border-bottom: 1px solid rgb(51 51 51 / 70%);
}

.is-sticky  .main-navigation {
    position: fixed;
    background: #fff;
    top: 0;
}

/****************** Top Header *****************/


.top-header {
    padding: 8px 0px;
    background: #F5F8F8;
    color: rgb(51 51 51 / 50%);
}

.white-header .top-header {
    padding: 8px 0px;
    background: #333333;
    color: #CED0D0;
}

.top-header-grid{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.auth-info {
    display: flex;
    align-items: center;
}

.contact-info span {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.01em;
    margin-right: 45px;
}

.white-header .contact-info span img{
opacity: 1;
}
.contact-info span img {
    margin-right: 10px;
    opacity: 0.5;
}

.search-icon {
    margin-right: 40px;
    cursor: pointer;
}

.auth-login {
    display: flex;
    align-items: center;
}

.white-header .auth-login a {
    color: #CED0D0;
}

.auth-login a {
    color: rgb(51 51 51 / 50%);
    margin-left: 30px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em;
    text-decoration: none;
}

.white-header .auth-login a:hover {
    color: #CED0D0!important;
}

.auth-login a:hover {
    color: rgb(51 51 51 / 50%)!important;
}

.auth-login a span {
display: inline-block;
margin-right: 20px;
}

.language-dropdown {
    margin-right: 20px;
}

.language-dropdown .btn, .language-dropdown .btn:hover, .language-dropdown .btn:focus, .language-dropdown .show>.btn-success.dropdown-toggle:focus, .language-dropdown .btn-success:active:focus {
    background: transparent;
    border-color: transparent;
    box-shadow: none;

    padding: 0px;
        position: relative;
}

.language-dropdown .dropdown-toggle::after{
    border: none;
    content: "";
    background: url(../../assets/images/header/arrow.svg);
    position: absolute;
    width: 9px;
    height: 5px;
    background-size: cover;
    right: -15px;
    top: 50%;
    transform: translateY(-50%);
    margin-left:0px;
}

.language-mobile{
    display: none;
}

/******************** Main navigation **************************/

.main-navigation {
    position: absolute;
    width: 100%;
    top: 42px;
    padding: 21px 0px 19px;
    border-bottom: 2px solid #F5F8F8;
    
}

.white-header .main-navigation {
    border-bottom: 1px solid rgb(245 248 248 / 20%);
}

.main-navigation-grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.menu-icon {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
}

.menu-icon span {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em; 
    color: #333333;
    margin-right: 26px;
}

.white-header .menu-icon span {
    color: #FFFFFF;
}

.menu-grid {
    position: absolute;
    right:63px;
    transition: all 0.2s;
    opacity: 0;
}

.menu-grid a, .menu-grid .nav-item span {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #333333;
    display: inline-block;
    padding: 0px 35px;
    cursor: pointer;
}

.white-header .menu-grid a, .white-header .menu-grid .nav-item span {
    color: #ffffff;
}

.menu-grid a:hover, .menu-grid a:focus, .menu-grid .nav-item span:hover, .menu-grid .nav-item span:focus {
    color: rgb(145 145 145 / 90%)!important;
}


.white-header .menu-grid a:hover, .white-header .menu-grid a:focus, .white-header .menu-grid .nav-item span:hover, .white-header .menu-grid .nav-item span:focus {
    color: rgba(255, 255, 255, 0.60)!important;
}


.open-menu .menu-grid {
    opacity: 1;
}

.open-menu .menu-icon span, .open-menu .menu-icon img{
display: none;
}

.white-header .main-navigation.open-menu{
    background: rgba(51, 51, 51, 0.7);
border-bottom: 1px solid rgba(51, 51, 51, 0.7);
}

.open-menu .logo{
    opacity: 0;
}

.open-menu .menu-icon:before {
    content: "";
    background: url(../../assets/images/header/close-dark.svg);
    width: 20px;
    height: 20px;
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    right: 5px;
}

.white-header .open-menu .menu-icon:before {
    content: "";
    background: url(../../assets/images/header/close.svg);
    width: 20px;
    height: 20px;
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    right: 5px;
}

.mobile-icons{
    display: none;
}

/******************** Search Container **************************/

.searc-container {
    position: fixed;
    background: rgba(0, 0, 0, 0.8);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-content {
    max-width: 437px;
    width: 100%;
}

.search-content img {
    margin: 0px auto;
    display: block;
    margin-bottom: 38px;
}

.search-content form input {
    background: #f5f8f8;
    border-radius: 122px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;

    letter-spacing: -0.01em;

    color: #ced0d0;
    height: 54px;
    padding: 10px 24px;
}

.search-content form input:focus{
    background: #f5f8f8;
    box-shadow: none;
    border-color: #f5f8f8;
}

.search-content form input::placeholder {
    color: #ced0d0;
}


.search-content form {
    position: relative;
}

.search-content form .btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 45px;
    height: 45px;
    background: #f35c5c;
    border-radius: 100%;
    border-color: #f35c5c;
    line-height: 45px;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    right: 5px;
}

.search-content form .btn:focus {
    box-shadow: none;
}

.search-content form .btn img {
    margin: 0px auto;
}

.search-close-btn {
    background: #6243CE;
    width: 45px;
    height: 45px;
    opacity: 1;
    border-radius: 100%;
    padding: 0;
    display: flex;
    align-items: center;
    position: absolute;
    right: 20%;
    top: 20%;
    cursor: pointer;
}

.search-close-btn img {
    margin: 0px auto;
}





/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/









@media screen and (max-width:767px){

    html {
        overflow-x: hidden;
    }

    .top-header{
        display: none;
    }

    .main-navigation {
        top: 0;
        padding: 19px 0px 19px;
    }

    .logo img {
        width: 100%;
    }
    .logo {
        width: 100px;
    }

    .menu-icon span{
        display: none;
    }

    .mobile-icons{
        display: flex;
        align-items: center;
        position: absolute;
        right: 50px;
        z-index: 9;
    }

    .mobile-icons span {
        display: inline-block;
        margin: 0px 15px;
        cursor: pointer;
    }


    .search-content {
        padding: 0px 15px;
    }

    .search-close-btn {
        right: 12px;
        top: 36px;
    }

    .search-content img {
        margin-bottom: 30px;
        width: 120px;
    }

    .search-content form .btn img {
        width: auto;
    }

    .search-close-btn img {
        width: auto;
        margin: 0px auto;
    }

    .menu-icon {
        z-index: 99;
    }

    .open-menu .menu-icon {
        z-index: 99;
        background: #6243CE;
        width: 45px;
        height: 45px;
        border-radius: 100%;
        position: fixed;
        right: 12px;
        top: 36px;
    }

    .menu-grid{
        display: none;
    }
    .open-menu .menu-grid {
        position: fixed;
        top: 0;
        background: rgb(0 0 0 / 90%);
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 9;
        display: block;
    }

    .main-navigation.open-menu {
        background: transparent;
        border-bottom: 1px solid transparent;
    }

    .main-navigation .nav {
        margin-top: 115px;
    }

    .nav-item {
        width: 100%;
        text-align: center;
    }

    .menu-grid a,  .menu-grid .nav-item span {
        font-weight: 700;
        padding: 21px 20px;
        color: #fff;
    }

    .open-menu .menu-icon:before {
        content: "";
        background: url(../../assets/images/header/close.svg);
        width: 20px;
        height: 20px;
        background-size: cover;
        background-repeat: no-repeat;
        display: block;
        position: absolute;
        right: 5px;
    }
    .open-menu .menu-icon:before {
        right: auto;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }

    .language-mobile{
        display: block;
        padding-top: 40px;
    }

    .language-mobile .contact-info span {
        display: block;
        margin: 0;
        text-align: center;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: -0.01em;
        color: #FFFFFF;
        padding-bottom: 10px;
    }
    .language-mobile .language-dropdown .dropdown {
        text-align: center;
    }

    .language-mobile .language-dropdown {
        margin: 0;
        padding-top: 40px;
    }

    .language-mobile .show>.btn-success.dropdown-toggle{
        background: transparent;
        border-color: transparent;
        box-shadow: none; 
    }

    .language-mobile .dropdown-menu {
        background: transparent;
    }
    .language-mobile .dropdown-menu .dropdown-item {
        text-align: left;
        display: block;
        margin: 0;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: -0.01em;
        color: #FFFFFF;
        padding: 10px 0px;
    }
    .contact-info span img{
        opacity: 1;
    }
}


