.consultation-popup .modal-lg {
    max-width: 828px;
}

.consultation-popup .modal-content {
    background: #FFFFFF;
    box-shadow: 0px 24.5184px 32.0625px rgb(0 0 0 / 10%);
    border-radius: 20px;
    border: none;
}

.consultation-popup .modal-header {
        padding: 0;
        border: none;
        position: absolute;
        right: -10px;
        top: -10px;
        z-index: 9;

}

.consultation-popup .modal-body{
    padding: 0;
}

.modal-backdrop {
    opacity: 0.8!important;
}

.consultation-popup .modal-header .btn-close {
    background: #6243CE;
    width: 45px;
    height: 45px;
    opacity: 1;
    border-radius: 100%;
    padding: 0;
}


.consultation-popup-grid {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.consultation-popup-content {
    padding: 50px 40px 50px 50px;
    max-width: 440px;
    width: 100%;
}

.modal-body {
    padding: 0;
}

.consultation-popup-cover {
    width: calc(100% - 440px);
}

.consultation-popup-content h2 {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    /* identical to box height, or 40px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    padding-bottom: 30px;
}

.consultation-popup-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* or 150% */

    letter-spacing: -0.01em;


    /* text */

    color: #333333;

    opacity: 0.7;
    margin: 0;
    padding-bottom: 30px;
}

.consultation-popup-content form input {
    background: #F5F8F8;
    border-radius: 122px;
    height: 54px;
    border-color: #F5F8F8;
    margin-bottom: 11px;
    padding: 10px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #CED0D0;
    box-shadow: none!important;
}

.consultation-popup-content form input:focus {
    box-shadow: none!important;
    border-color: #F5F8F8;
    background: #F5F8F8;
}

.consultation-popup-content form input::placeholder {
    color: #CED0D0;
}

.consultation-popup-content .btn {
    background: #f35c5c;
    /* btn shadows */

    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.12), 0px 2px 4px -1px rgba(0, 0, 0, 0.07);
    border-radius: 33px;
    width: 100%;
    border-color: #f35c5c;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height, or 150% */

    letter-spacing: -0.01em;

    color: #ffffff;
    height: 54px;
    margin-top: 24px;
}

.consultation-popup-content .btn:hover, .consultation-popup-content .btn:focus {
    box-shadow: none!important;
    background: #6243CE;
    border-color: #6243CE;
}

.consultation-popup .btn-close:before {
    content: "";
    background: url(../../assets/images/header/close-white.svg);
    width: 18px;
    height: 18px;
    background-size: cover;
    display: block;
    background-repeat: no-repeat;
    margin: 0px auto;
}




/***************************** Video Popup ************************/



.video-popup .modal-lg {
    max-width: 1152px;
}

.video-popup .modal-content {
    background: #121212;
    box-shadow: 0px 24.5184px 32.0625px rgb(0 0 0 / 10%);
    border-radius: 0px;
    border: none;
}


.video-popup .modal-body{
    padding: 0;
}


.video-popup .modal-header .btn-close {
    background: #6243CE;
    width: 45px;
    height: 45px;
    opacity: 1;
    border-radius: 100%;
    padding: 0;
}


.video-popup .btn-close:before {
    content: "";
    background: url(../../assets/images/header/close-white.svg);
    width: 18px;
    height: 18px;
    background-size: cover;
    display: block;
    background-repeat: no-repeat;
    margin: 0px auto;
}



.video-popup .modal-header {
    position: absolute;
    padding: 0;
    border: 0;
    left: 50%;
    bottom: -50px;
    transform: translateX(-50%);
}



/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/









@media screen and (max-width:767px){

.consultation-popup .modal-lg {
    margin: 1rem;
}

.consultation-popup-content {
    padding: 30px;
    max-width: 100%;
    width: 100%;
}

.consultation-popup-cover {
    width: 100%;
}

.consultation-popup-cover img {
    height: 200px;
    width: 100%;
    object-fit: cover;
    object-position: bottom;
}

.consultation-popup .modal-header {
    right: 0px;
    top: -10px;
}

.consultation-popup .modal-body {
    padding: 0;
    border-bottom-left-radius: 20px;
    overflow: hidden;
}
.consultation-popup-content h2 {
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    padding-bottom: 15px;
}

.consultation-popup-content p {
    padding-bottom: 15px;
}

.consultation-popup-content form input {
    font-size: 14px;
}

.consultation-popup-content .btn{
    margin-top: 0;
}

.video-popup .modal-body > div {
    height: 300px!important;
}

}