.contact-page {
    padding-top: 28px;
}

.contact-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.contact-content {
    width: calc(100% - 500px);
}

.contact-form {
    max-width: 450px;
    width: 100%;
    background: #ffffff;
    /* cards shadow */

    box-shadow: 0px 24.5184px 32.0625px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding: 50px 50px 23px;
    position: relative;
    margin-bottom: -40px;
}

.contact-content h2 {
    padding-top: 20px;
    margin: 0;
    padding-bottom: 30px;
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #333333;
}

.contact-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 20px;
}

.contact-info-grid {
    width: 48%;
    display: flex;
    position: relative;
    padding-left: 30px;
}

.contact-info-grid img {
    position: absolute;
    top: 5px;
    left: 0;
}

.contact-info-text span {
    display: block;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #6E6F6F;
    max-width: 280px;
}

.location-place img {
    width: 100%;
}

.contact-info-text p {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    color: #333333;
    margin: 0;
    padding-top: 8px;
}




.location-place img {
    width: 100%;
}

.contact-info-text p {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;

    letter-spacing: -0.01em;
    text-transform: uppercase;

    color: #333333;
    margin: 0;
    padding-top: 8px;
}

.contact-form-content h2 {
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    /* or 31px */

    letter-spacing: 0.01em;

    /* text */

    color: #333333;
    margin: 0;
    padding-bottom: 20px;
}

.contact-form-content p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;


    letter-spacing: -0.01em;


    color: #6e6f6f;
    margin: 0;
    padding-bottom: 20px;
}

.contact-form-content form .form-control {
    background: #f5f8f8;
    border-radius: 122px;
    width: 100%;
    height: 54px;
    margin-bottom: 11px;
    border-color: #f5f8f8;
    padding: 10px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;


    letter-spacing: -0.01em;


    color: #ced0d0;
}

.contact-form-content form .form-control::placeholder {
    color: #ced0d0;
}

.contact-form-content form .btn {
    background: #f35c5c;
    border-radius: 33px;
    border-color: #f35c5c;
    width: 100%;
    height: 54px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #ffffff;
    margin-top: 10px;
    box-shadow: none!important;
    transition:  all 0.5s;
    position: relative;
}

.contact-form-content form textarea.form-control {
    height: 155px;
    border-radius: 10px;
}

.contact-form-content form .btn:after{
    content: "";
    background: url(../../assets/images/header/btn-arrow-white.svg);
    width: 16px;
    height: 16px;
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 8px;
    position: relative;
    top: 2px;
    display: none;

}

.contact-form-content form .btn:hover {
    background: #6243CE!important;
    border-color: #6243CE!important;
}

.contact-form-content form .btn:hover:after {
    display: inline-block;
}

.contact-form-content form .btn:focus {
    background: #CED0D0!important;
    border-color: #CED0D0!important;
}

.contact-form-content form .btn:focus:hover:after{
    display:none
}


/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/




@media screen and (max-width:767px){

    .contact-page {
        padding-top: 20px;
    }

    .contact-content h2 {
        font-size: 20px;
        padding-bottom: 20px;
    }

.contact-content {
    width: 100%;
}

.contact-content {
    padding-bottom: 45px;
}

.contact-form {
    z-index: 9;
    margin: 0px auto;
    margin-bottom: -40px;
    padding: 20px;
}
.contact-info-grid {
    width: 100%;
}
.contact-info-grid {
    padding-bottom: 20px;
}
.contact-form-content h2 {
    font-size: 20px;
}
.contact-form-content form .form-control{
    font-size:14px;
}
}