.orange-button{
    background: #F35C5C!important;
    border-radius: 74px!important;
    border-color: #F35C5C!important;
    margin: 0px 8px;
    max-width: 220px!important;
    height: 55px!important;
    width: 100%!important;
    font-weight: 700!important;
    font-size: 16px!important;
    line-height: 24px!important;
    letter-spacing: -0.01em!important;
    color: #FFFFFF!important;
    box-shadow: none!important;
    transition:  all 0.5s;
    position: relative;
}

.orange-button:after{
    content: "";
    background: url(../../assets/images/header/btn-arrow-white.svg);
    width: 16px;
    height: 16px;
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 8px;
    position: relative;
    top: 2px;
    display: none;

}

.orange-button:hover {
    background: #6243CE!important;
    border-color: #6243CE!important;
}

.orange-button:focus {
    background: #CED0D0!important;
    border-color: #CED0D0!important;
}

.orange-button:focus:hover:after{
    display:none
}

.orange-button:hover:after {
    display: inline-block;
}

.white-button {
    background: #ffffff!important;
    color: #F35C5C!important;
    border-color: #ffffff!important;
    border-radius: 74px!important;
    margin: 0px 8px;
    max-width: 220px!important;
    height: 55px!important;
    width: 100%!important;
    font-weight: 700!important;
    font-size: 16px!important;
    line-height: 24px!important;
    letter-spacing: -0.01em!important;
    box-shadow: none!important;
    transition:  all 0.5s;
    position: relative;
}

.white-button:after{
    content: "";
    background: url(../../assets/images/header/btn-arrow-white.svg);
    width: 16px;
    height: 16px;
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 8px;
    position: relative;
    top: 2px;
    display: none;

}


.white-button:hover {
    background: #6243CE!important;
    border-color: #6243CE!important;
    color: #ffffff!important;
}

.white-button:focus {
    background: #CED0D0!important;
    border-color: #CED0D0!important;
    color: #ffffff!important;
}


.white-button:focus:hover:after{
    display:none
}


.white-button:hover:after {
    display: inline-block;
}



.outline-button{
    background: transparent!important;
    border-radius: 74px!important;
    max-width: 220px!important;
    height: 55px!important;
    width: 100%!important;
    border-color: #f35c5c!important;
    font-weight: 700!important;
    font-size: 16px!important;
    line-height: 24px!important;
    letter-spacing: -0.01em!important;
    color: #f35c5c!important;
    box-shadow: none!important;
    transition:  all 0.5s;
    position: relative;
}


.outline-button:after{
    content: "";
    background: url(../../assets/images/header/btn-arrow-blue.svg);
    width: 16px;
    height: 16px;
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 8px;
    position: relative;
    top: 2px;
